import express from 'express';
const colors = require('colors');
const app = express();

const PORT = 3000;


app.get('/', (req, res) => {
    res.status(200).json({
        ok: true,
        mensaje: 'Todo bien'
    });
});



app.listen(PORT, () => {
    console.log(`Servidor corriendo en PORT: ${PORT}`.rainbow);
});